<template>
  <div class="addBank">
    <j-header title="添加银行卡"></j-header>
    <main>
      <ul>
        <li>
          <p>手机号</p>
          <jun-input v-model="form.phone"
            placeholder="请输入手机号"
            clear>
          </jun-input>
        </li>
        <li>
          <p>持卡人姓名</p>
          <jun-input v-model="form.account"
            placeholder="请输入持卡人姓名"
            clear>
          </jun-input>
        </li>
        <li>
          <p>银行卡号</p>
          <jun-input v-model="form.bank_card"
            placeholder="请输入银行卡号"
            clear>
          </jun-input>
        </li>
        <li>
          <p>开户名称</p>
          <jun-input v-model="form.bank_branch"
            placeholder="请输入开户名称"
            clear>
          </jun-input>
        </li>
        <li @click="showBank = true">
          <p>开户银行</p>
          <jun-input v-model="bankName"
            placeholder="请输入开户银行"
            disabled
            clear>
          </jun-input>
        </li>
        <li @click="showCity = true">
          <p>开户地址</p>
          <jun-input v-model="city"
            placeholder="请输入开户地址"
            disabled
            clear>
          </jun-input>
        </li>
        <li>
          <p>图形验证码</p>
          <jun-input v-model="imgCode"
            placeholder="请输入图形验证码"
            clear>
          </jun-input>
          <img-code type='0'/>
        </li>
        <li>
          <p>手机验证码</p>
          <jun-input v-model="form.smscode"
            placeholder="请输入手机验证码"
            clear>
          </jun-input>
          <send-code :data='{ type: 0, phone: form.phone, code: imgCode }'/>
        </li>
      </ul>
      <button @click="confirm">确认添加</button>
    </main>
    <j-city v-model="showCity" @city="cityInfo" title="开户地址"/>
    <j-bank v-model="showBank" @getBank="getBank"/>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JunInput from '@/components/JunInput'
import imgCode from '@/components/imgCode'
import sendCode from '@/components/sendCode'
import JCity from '@/components/JCity'
import JBank from '@/components/JBank'

export default {
  name: 'addBank',
  components: {
    JHeader,
    JunInput,
    imgCode,
    sendCode,
    JCity,
    JBank
  },
  data () {
    return {
      showCity: false,
      showBank: false,
      imgCode: '',
      city: '',
      bankName: '',
      form: {
        phone: '',
        account: '',
        bank_card: '',
        bank_branch: '',
        bank_code: '',
        province: '',
        city: '',
        area: '',
        smscode: '',
        type: 10
      },
      validate: {
        account: '持卡人姓名不能为空',
        bank_code: '开户银行不能为空',
        bank_branch: '开户名称不能为空',
        bank_card: '银行卡号不能为空',
        phone: '手机号不能为空',
        smscode: '手机验证码不能为空',
        province: '请选择开户地址',
        city: '请选择开户地址',
        area: '请选择开户地址'
      }
    }
  },
  created () {
  },
  methods: {
    cityInfo (obj) {
      var str = ''
      for (var key in obj) {
        if (obj[key]) {
          str += obj[key] + '/'
          this.form[key] = obj[key]
        }
      }
      this.city = str.substring(0, str.length - 1)
    },
    getBank (obj) {
      this.bankName = obj.name
      this.form.bank_code = obj.code
    },
    async verify () {
      for (var key in this.form) {
        if (key !== 'type') {
          if (!this.form[key]) {
            this.$toast({ message: this.validate[key], position: 'top' })
            return false
          }
        }
      }
    },
    async confirm () {
      const condition = await this.verify()
      condition !== false && this.request({
        url: '/bank',
        method: 'post',
        data: this.form
      }).then(res => {
        this.$toast({
          message: res.message,
          position: 'top'
        })
        res.code === 1 && this.$router.go(-1)
      })
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
