<template>
  <!--居住地址三级联动选项-->
  <transition name="outside" mode="out-in">
    <section class="showChose" v-show="showChose">
      <transition name="inside" mode="out-in">
        <section class="address" v-show="showChose">
          <section class="title">
            <h4>{{ title }}</h4>
            <span @click="closeAdd()">×</span>
          </section>
          <section class="title">
            <div class="area" @click="provinceSelected()">{{Province?Province:info[province-1].name}}</div>
            <div class="area" @click="citySelected()" :class="City?'':'active'">{{City?City:'请选择'}}</div>
            <div class="area" @click="districtSelected()" :class="District?'':'active'" v-show="City">{{District?District:'请选择'}}</div>
          </section>
          <ul>
            <li class="addList" v-for="(v,k) in info" @click="getProvinceId(v.id, v.name, k)" v-show="showProvince" :class="v.selected ? 'active' : ''" :key="v.name + 1">{{v.name}}</li>
            <li class="addList" v-for="(v,k) in showCityList" @click="getCityId(v.id, v.name, k)" v-show="showCity" :class="v.selected ? 'active' : ''" :key="v.name + 2">{{v.name}}</li>
            <li class="addList" v-for="(v,k) in showDistrictList" @click="getDistrictId(v.id, v.name, k)" v-show="showDistrict" :class="v.selected ? 'active' : ''" :key="v.name + 3">{{v.name}}</li>
          </ul>
        </section>
      </transition>
    </section>
  </transition>
</template>

<script>
// @ is an alias to /src
import info from './city'

export default {
  name: 'myAddress',
  data () {
    return {
      // showChose: true,
      showProvince: true,
      showCity: false,
      showDistrict: false,
      showCityList: false,
      showDistrictList: false,
      province: 1,
      city: 3,
      district: 57,
      GetProvinceId: 2,
      District: false,
      Province: false,
      City: false,
      // v-for循环判断是否为当前
      selected: false,
      info
    }
  },
  model: {
    prop: 'showChose',
    event: 'change'
  },
  props: {
    showChose: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '居住地址'
    }
  },
  created () {
  },
  methods: {
    choseAdd () {
      // this.showChose = true
    },
    closeAdd () {
      this.$emit('change', false)
      // this.showChose = false
    },
    _filter (add, name, code) {
      let result = []
      for (let i = 0; i < add.length; i++) {
        if (code === add[i].id) {
          result = add[i][name]
        }
      }
      return result
    },
    getProvinceId (code, input, index) {
      this.province = code
      this.Province = input
      this.$emit('city', { province: this.Province, city: this.City, area: this.District })
      this.showProvince = false
      this.showCity = true
      this.showDistrict = false
      this.showCityList = this._filter(this.info, 'city', this.province)
      // 点击选择当前
      this.info.map(a => {
        a.selected = false
      })
      this.info[index].selected = true
    },
    provinceSelected () {
      // 清除市级和区级列表
      this.showCityList = false
      this.showDistrictList = false
      // 清除市级和区级选项
      this.City = false
      this.District = false
      // 选项页面的切换
      this.showProvince = true
      this.showCity = false
      this.showDistrict = false
    },
    getCityId (code, input, index) {
      this.city = code
      this.City = input
      this.$emit('city', { province: this.Province, city: this.City, area: this.District })
      this.showProvince = false
      this.showCity = false
      this.showDistrict = true
      this.showDistrictList = this._filter(this.showCityList, 'district', this.city)
      // 选择当前添加active
      this.showCityList.map(a => {
        a.selected = false
      })
      this.showCityList[index].selected = true
    },
    citySelected () {
      this.showProvince = false
      this.showCity = true
      this.showDistrict = false
    },
    getDistrictId (code, input, index) {
      this.district = code
      this.District = input
      this.$emit('city', { province: this.Province, city: this.City, area: this.District })
      // 选择当前添加active
      this.showDistrictList.map(a => {
        a.selected = false
      })
      this.showDistrictList[index].selected = true
      // 选取市区选项之后关闭弹层
      // this.showChose = false
      this.$emit('change', false)
    },
    districtSelected () {
      this.showProvince = false
      this.showCity = false
      this.showDistrict = true
    }
  }
}
</script>

<style scope lang="scss">
  @import './index.scss';
  .outside-enter-active, .outside-leave-active {
    transition: all 0.5s;
  }
  .outside-enter, .outside-leave-active {
    // transform: translateY(5rem);
    opacity: 0;
  }
  .inside-enter-active, .inside-leave-active {
    transition: all 0.5s;
  }
  .inside-enter, .inside-leave-active {
    transform: translateY(6rem);
  }
</style>
